// =============================================================================
// URL ENRICHMENT INTERNAL HANDLER
// =============================================================================
// Internal API called by metered url-enrich endpoint after payment verification
// This should only be called internally with valid x-request-id

import { NextRequest, NextResponse } from 'next/server';
import { getOrCreateRequestId, addRequestIdToResponse } from '@/lib/request-id';

/**
 * Simple deterministic URL enrichment for demo purposes
 * This simulates what a real URL enrichment API would do
 */
function enrichUrl(url: string): {
  url: string;
  domain: string;
  title: string;
  summary: string;
  enrichedAt: string;
} {
  try {
    const parsed = new URL(url);
    const domain = parsed.hostname;
    
    // Generate deterministic mock data based on domain
    const titles: Record<string, string> = {
      'example.com': 'Example Domain',
      'google.com': 'Google',
      'github.com': 'GitHub: Let\'s build from here',
      'wikipedia.org': 'Wikipedia, the free encyclopedia',
      'twitter.com': 'X (formerly Twitter)',
      'x.com': 'X (formerly Twitter)',
    };
    
    const summaries: Record<string, string> = {
      'example.com': 'This domain is for use in illustrative examples in documents. You may use this domain in literature without prior coordination or asking for permission.',
      'google.com': 'Search the world\'s information, including webpages, images, videos and more. Google has many special features to help you find exactly what you\'re looking for.',
      'github.com': 'GitHub is where over 100 million developers shape the future of software, together. Contribute to the open source community, manage your Git repositories, review code like a pro.',
      'wikipedia.org': 'Wikipedia is a free online encyclopedia, created and edited by volunteers around the world and hosted by the Wikimedia Foundation.',
      'twitter.com': 'From breaking news and entertainment to sports and politics, get the full story with all the live commentary.',
      'x.com': 'From breaking news and entertainment to sports and politics, get the full story with all the live commentary.',
    };
    
    // Find matching entry (with subdomain fallback)
    const domainParts = domain.split('.');
    let baseDomain = domain;
    if (domainParts.length > 2) {
      baseDomain = domainParts.slice(-2).join('.');
    }
    
    const title = titles[baseDomain] || titles[domain] || `${domain.charAt(0).toUpperCase() + domain.slice(1).split('.')[0]} - Website`;
    const summary = summaries[baseDomain] || summaries[domain] || `A website at ${domain}. Visit to learn more about their offerings and services.`;
    
    return {
      url,
      domain,
      title,
      summary,
      enrichedAt: new Date().toISOString(),
    };
  } catch (error) {
    // Invalid URL - return basic response
    return {
      url,
      domain: 'unknown',
      title: 'Unknown URL',
      summary: 'Could not parse or enrich the provided URL.',
      enrichedAt: new Date().toISOString(),
    };
  }
}

/**
 * POST /api/demo/url-enrich-internal
 * 
 * Internal endpoint for URL enrichment
 * Called by the metered url-enrich endpoint after payment verification
 */
export async function POST(request: NextRequest) {
  const requestId = getOrCreateRequestId(request);
  
  try {
    const body = await request.json();
    const { url } = body;
    
    if (!url || typeof url !== 'string') {
      return NextResponse.json(
        {
          error: 'Invalid request',
          code: 'INVALID_URL',
          message: 'url field is required and must be a string',
          requestId,
        },
        { status: 400 }
      );
    }
    
    // Validate URL format
    try {
      new URL(url);
    } catch {
      return NextResponse.json(
        {
          error: 'Invalid URL format',
          code: 'INVALID_URL_FORMAT',
          message: 'The provided URL is not a valid HTTP/HTTPS URL',
          requestId,
        },
        { status: 400 }
      );
    }
    
    // Enrich the URL
    const enriched = enrichUrl(url);
    
    const response = NextResponse.json(enriched, { status: 200 });
    addRequestIdToResponse(response, requestId);
    return response;
    
  } catch (error) {
    return NextResponse.json(
      {
        error: 'Internal server error',
        code: 'INTERNAL_ERROR',
        message: error instanceof Error ? error.message : 'Unknown error',
        requestId,
      },
      { status: 500 }
    );
  }
}

